<template>
  <b-card>
    <div class="d-flex justify-content-between mb-2">
      <download-excel
        :data="exportItems"
        :fields="exportFields"
        :filename="'Employee'"
        :sheetname="'Employee'"
        class="cursor-pointer"
      >
        <b-button
          variant="outline-secondary"
          size="sm"
          class="mr-1"
          style="padding: 0.6rem"
        >
          <feather-icon icon="FileIcon" size="20" class="text-success" />
          <label style="font-size: 14px; margin-left: 5px; cursor: pointer"
            >Export</label
          >
        </b-button>
      </download-excel>
      <b-link :to="{ name: 'hrm/employee/add' }">
        <b-button variant="primary">Add Employee</b-button>
      </b-link>
    </div>
    <div class="d-flex justify-content-between align-items-center mb-1">
      <div class="d-flex align-items-center">
        <label for="" class="mr-1" style="font-size: 14px">Sort</label>
        <b-form-select
          id="perPageSelect"
          v-model="perPage"
          size="sm"
          :options="pageOptions"
          class="w-100"
        />
      </div>
      <div class="d-flex align-items-center">
        <label for="" class="mr-1" style="font-size: 14px">Search</label>
        <b-form-input
          id="searchInput"
          v-model="filter"
          size="sm"
          type="search"
          placeholder="Search"
        />
      </div>
    </div>
    <b-row>
      <b-col cols="12">
        <b-table
          striped
          hover
          responsive
          :per-page="perPage"
          :current-page="currentPage"
          :items="items"
          :fields="fields"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
          :filter="filter"
          @filtered="onFiltered"
        >
          <template #cell(name)="data">
            <b-link :to="{ name: 'hrm/employee/detail' }">
              <span class="text-primary">
                {{ data.value }}
              </span>
            </b-link>
          </template>
          <template #cell(profile_photo_url)="data">
            <b-avatar :src="data.value" />
          </template>

          <template #cell(is_active)="data">
            <b-badge :variant="status[1][data.value]">
              {{ status[0][data.value] }}
            </b-badge>
          </template>
        </b-table>
      </b-col>

      <b-col cols="12">
        <div class="d-flex justify-content-between align-items-center">
          <span>
            Showing
            {{ perPage > totalRows ? totalRows : perPage }} of
            {{ totalRows }} entries
          </span>
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            align="right"
            size="sm"
            class="my-0"
          />
        </div>
      </b-col>
    </b-row>
  </b-card>
</template>

<script>
export default {
  data() {
    return {
      perPage: 5,
      pageOptions: [3, 5, 10],
      totalRows: 1,
      currentPage: 1,
      sortBy: "",
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      fields: [
        {
          key: "hrcode",
          label: "HR Code",
        },
        {
          key: "profile_photo_url",
          label: "Avatar",
        },
        { key: "name", label: "Full Name", sortable: true },
        "email",
        { key: "department.name", label: "Department", sortable: "true" },
        { key: "team.name", label: "Position", sortable: "true" },
        { key: "is_active", label: "Status", sortable: true },
      ],
      items: [],
      status: [
        {
          1: "Active",
          2: "Inactive",
        },
        {
          1: "light-success",
          2: "light-danger",
        },
      ],

      // export excel
      exportFields: {
        "HR Code": "hrcode",
        Name: "name",
        Email: "email",
        Department: "department.name",
        Position: "team.name",
        Status: "is_active",
      },
      exportItems: [],
    };
  },
  computed: {
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => ({ text: f.label, value: f.key }));
    },
  },
  mounted() {
    // Set the initial number of items
    this.totalRows = this.items.length;

    // fetch data
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.callApi({
        method: "GET",
        url: "/users/getAll",
        success: (response) => {
          this.items = response.result.data;
          this.totalRows = this.items.length;

          // export excel
          this.exportItems = response.result.data;
        },
        error: (error) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Failed to fetch data",
              variant: "error",
            },
          });
        },
      });
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
  },
};
</script>
